import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link, useNavigate } from 'react-router-dom'
import { Button, Container } from 'react-bootstrap'
import '../../style.css'
import { userSlice } from '../../features/userSlice'
import { partnersSlice } from '../../features/partnersSlice'

const Pending = () => {
  const user = useSelector((state) => state.user.user)
  const navigate = useNavigate()
  const dispatch = useDispatch()
  return (
    <div>
      <div
        style={{ height: '100vh',overflowY:'hidden'}}
        className=" d-flex flex-column"
      >
        <main className="h-100 text-center position-relative fs-3 d-flex  flex-column justify-content-center">
          <div className="cirlce-warpper">
            <div className="circle"></div>
          </div>
          <Container>
            <nav
              className="w-100 h1 d-flex h-100 align-items-center justify-content-center text-center text-md-start"
              style={{ minHeight: '5rem', maxHeight: '7rem' }}
            >
              Welcome &nbsp;<i className="name-color">&quot;{user && user.name}&quot;</i> &nbsp;
              You Account is pending for approval
            </nav>
          </Container>
          <Container>
            <p>
              We are evaluating your profile. This may take 24 working hours. Once your profile is
              approved, you can access the site. Thank you for your patience.
            </p>
          </Container>
          <div className>
            <Button
              onClick={() => {
                navigate('/login')
                localStorage.clear()
                dispatch(userSlice.actions.clearUser())
                dispatch(partnersSlice.actions.clearPartners())
              }}
              className="myCustomBtn px-3 py-2"
            >
              Logout
            </Button>
          </div>
          <div className="">
            <Link
              className="name-colortn fs-5"
              to={'https://adinath.site/contact-us/'}
            >
              Contact Us
            </Link>
          </div>
        </main>
        <footer className="bg-black justify-self-end foot">
          <div className="container">
            <div className="row">
              <div className="col-12 text-center text-white py-3">
                <Link
                  className="text-white"
                  to="https://adinath.site/"
                  target="_blank"
                  rel="noopener noreferrer"
                  style={{
                    textDecoration: 'none',
                  }}
                >
                  Adinath Group Online
                </Link>
                <span className="ms-1">&copy; 2023 Adinath Group.</span>
              </div>
            </div>
          </div>
        </footer>
      </div>
    </div>
  )
}

export default Pending
